.App {
    font-family: Ubuntu;
}

a {
    cursor: pointer;
}
header {
    background: #FFFFFF;
}
.tituloComum:after {
    content: '_____';
    position: absolute;
    bottom: 0;
    left: 0;
    font-weight: bold;
    color: #FF9F1C;   
}
.header {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.wrapper {
    margin-right: auto;
    margin-left:  auto;
    max-width: 960px;
    padding-right: 10px;
    padding-left:  10px;
}
.search-bar {
    position: relative;
    width: 450px;
}
.search-bar input {
    width: 100%;
    padding: 3px 0;
    font-size: 13px;
    border: 0;
    border-bottom: 1px solid #7A7A7A;
}
.search-bar button {
    position: absolute;
    right: 3px;
    top: 0;
    border: 0;
    background: Transparent;
}
.account {
    width: 360.8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.account .block-1 {
    width: 146px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.account .block-1 .myaccount button {
    background: transparent;
    border: 0;
}
.cart {
    display: flex;
    align-items: center;
}
.cart button {
    background: transparent;
    border: 0;
}
.cart span {
    width: 14px;
    height: 14px;
    background: #2EC4B6;
    font-size: 11px;
    font-weight: 500;
    color: white;
    border-radius: 100px;
    text-align: center;
}
.block-2 button {
    background: transparent;
    border: 0;
}
.block-2 img {
    object-fit: none;
    width: 25.2px;
    height: 18px;
    margin-right: 20px;
}
.brasil img {
    object-position: 0 0;
}
.espanha img {
    object-position: -46px 0;
}
.inglaterra img {
    object-position: -90px 0;
}

.headerMobile {
    display: none;
}

/* MOBILE */


@media only screen and (max-width: 1024px) {
    .header {
        display: none;
    }
    .headerMobile {
        display: block;
        padding: 0 10px;
    }
    .headerMobile .rowOne{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 80px;
    }
    .headerMobile .rowTwo .search-bar {
        width: 100%;
        margin-bottom: 10px;
    }
}