body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    font-family: Ubuntu;
}

a {
    cursor: pointer;
}
header {
    background: #FFFFFF;
}
.tituloComum:after {
    content: '_____';
    position: absolute;
    bottom: 0;
    left: 0;
    font-weight: bold;
    color: #FF9F1C;   
}
.header {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.wrapper {
    margin-right: auto;
    margin-left:  auto;
    max-width: 960px;
    padding-right: 10px;
    padding-left:  10px;
}
.search-bar {
    position: relative;
    width: 450px;
}
.search-bar input {
    width: 100%;
    padding: 3px 0;
    font-size: 13px;
    border: 0;
    border-bottom: 1px solid #7A7A7A;
}
.search-bar button {
    position: absolute;
    right: 3px;
    top: 0;
    border: 0;
    background: Transparent;
}
.account {
    width: 360.8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.account .block-1 {
    width: 146px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.account .block-1 .myaccount button {
    background: transparent;
    border: 0;
}
.cart {
    display: flex;
    align-items: center;
}
.cart button {
    background: transparent;
    border: 0;
}
.cart span {
    width: 14px;
    height: 14px;
    background: #2EC4B6;
    font-size: 11px;
    font-weight: 500;
    color: white;
    border-radius: 100px;
    text-align: center;
}
.block-2 button {
    background: transparent;
    border: 0;
}
.block-2 img {
    object-fit: none;
    width: 25.2px;
    height: 18px;
    margin-right: 20px;
}
.brasil img {
    object-position: 0 0;
}
.espanha img {
    object-position: -46px 0;
}
.inglaterra img {
    object-position: -90px 0;
}

.headerMobile {
    display: none;
}

/* MOBILE */


@media only screen and (max-width: 1024px) {
    .header {
        display: none;
    }
    .headerMobile {
        display: block;
        padding: 0 10px;
    }
    .headerMobile .rowOne{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 80px;
    }
    .headerMobile .rowTwo .search-bar {
        width: 100%;
        margin-bottom: 10px;
    }
}
.banner {
    width: 100%;
    background: #011627;
}
.banner img {
    width: 100%;
    height: auto;
}
.banner ul {
    padding: 0;
}
.banner ul li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    position: relative;
}
.banner ul li .left, .banner ul li .right {
    width: 50%;
}
.banner .left {
    height: 100%;
}
.banner .left h3 {
    font-size: 45px;
}
.banner h3 {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 32px;
    color: white;
    position: absolute;
    width: 30%;
    left: 20%;
    top: 35%;
}

.banner h3:after {
    content: '___';
    position: absolute;
    bottom: -20px;
    left: 0;
    font-weight: bold;
    color: #FF9F1C;   
}

.slider-control-centerleft {
    left: 15% !important;
}
.slider-control-centerright {
    right: 15% !important;
}
.slider-list {
    cursor: default !important;
}

.bannerMobile {
    display: none;
}
/* MOBILE */

@media only screen and (max-width: 1024px) {
    .bannersDesk {
        display: none;
    }
    .bannerMobile {
        display: block;
    }
    .bannerMobile img {
        width: 100%;
        height: auto;
    }
}
.listagem ul {
    display: flex;
    justify-content: space-between;
}

.listagem ul li {
    list-style: none;
    width: 20%;
    text-align: center;
    padding: 20px;
}

.listagem ul li:hover {
    background: #F2F2F2;
}
.listagem ul li:hover .botaoComprar {
    opacity: 1 !important;
}
.listagem ul {
    padding: 0;
}
.listagem .flag, .listagemMobile .flag {
    text-align: right;
    height: 76px;
}

.listagem .imagemProduto, .listagemMobile .imagemProduto {
    width: 100%;
    height: 190px;
}
.listagem .imagemProduto img {
    width: 100%;
    height: auto;
}
.listagem .preco {
    text-align: center;
    margin-bottom: 10px;
}

.listagem .tituloProduto p { 
    font-size: 12px;
    text-transform: uppercase;
    color: #7A7A7A;
    text-align: center;
}

.listagem .preco .precoDe {
    text-decoration: line-through;
    color: #7A7A7A;
    font-size: 12px;
}

.listagem .preco .precoPor {
    font-weight: 500;
    line-height: normal;
    font-size: 18px;
    text-align: center;
    color: #2EC4B6;
}

.listagem .preco .ou {
    font-size: 11px;
    text-align: center;
    color: #7A7A7A;
}
.listagemMobile {
    display: none;
}
/* MOBILE */

@media only screen and (max-width: 1024px) {
    .listagem {
        display: none;
    }
    .listagemMobile {
        display: block;
        /* height: 470px; */
    }
    .listagemMobile .imagemProduto img {
        width: 100%;
        height: auto;
    }
    .slider-control-centerleft, 
    .slider-control-centerright {
        display: none;
    }
    .listagemMobile .slider {
        /* height: 490px !important; */
    }
    .listagemMobile .slider-list {
        /* height: 440px !important; */
    }
    .slider-slide {
        width: 50%;
        text-align: center;
    }
    .botaoComprar {
        opacity: 1 !important;
        margin-top: 10px !important;
    }
}
h1,h2,h3 {
    margin: 0;
}
button {
    cursor: pointer;
}
.footer {
    height: 223px;
    width: 100%;
    background: #011627;
    color: white;   
    border-bottom: 10px solid #2EC4B6;
}
.footer .loc h2 {
    position: relative;
    padding: 15px 0;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 20px;
    margin-bottom: 14px;
}
.footer .loc h2:after {
    content: '_____';
    position: absolute;
    bottom: 0;
    left: 0;
    font-weight: bold;
    color: #FF9F1C;
}
.footer .block-left {
    width: 70%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer .wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* height: 213px; */
}
.address {
    display: flex;
    justify-content: space-between;
    width: 370px;
    line-height: 23px;
}
.address h3 {
    font-size: 16px;
    margin: 0;
    line-height: 35px;
}
.address span {
    display: block;
    font-size: 16px;
}
.footer .contact {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    height: 180px;

}
.footer .contact button {
    position: relative;
}

.footer .contact img {
    vertical-align: middle;
    position: absolute;
    left: 11px;
    top: 11px; 
}

.footer .contact button {
    width: 195px;
    height: 38px;
    background: #FF9F1C;
    border-radius: 5px;
    text-align: center;
    text-transform: uppercase;
    font-size: 11.8202px;
    border: 0;
    color: white;
    font-weight: 500;
    margin-bottom: 30px;
}
.footer .contact button:last-child {
    margin-bottom: 0;
}

.footer .block-right {
    position: absolute;
    right: 0;
    bottom: 0;
 }


 /* MOBILE */

 
@media only screen and (max-width: 1024px) {
    .footer {
        height: 473px;
    }
    .footer .wrapper {
        justify-content: space-around;
        height: 403px;
    }
    .block-left {
        flex-direction: column;
    }
    .block-right {
        bottom: -50px !important;
        width: 100%;
        text-align: center;
    }
}

